/*Layout Styles*/
.page-banner {
    height: 30vh;
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    background-color: $gray;
    padding: 0 40px;
    h1 {
        margin: auto auto 0 auto;
        padding: 0;
        font-size: 3em;
        line-height: 1.2em;

        @media (min-width: 768px) {
            font-size: 3.2em;
        }

        @media (min-width: 992px) {
            font-size: 3.4em;
        }
    }

    p {
        margin: 0 auto auto auto;
    }
}

.bio-photo {
    width: 250px;
    height: 250px;
    border-radius: 50%;
}

@media (max-width: 768px) {
    ul.social-links {
        display: flex;
        justify-content: center;
        margin-top: 20px;

    }
}


.skills-list {
    list-style-type: none;
    width: auto;
    li {  
        display: inline-block;
        margin: 5px;
        i {
            background-color: $secondary;
            color: $white;
            padding: 15px 20px;
            border-radius: 20px;
        }
    }
}