body {
  font-family: "Poppins", Arial, serif;
  min-width: 100%;
  height: initial;
  line-height: 1;
  background-color: rgb(255, 255, 255);
}

/*Resets*/
html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font-size: 1em;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.entry-footer {
  background-color: rgb(255, 255, 255);
}

footer {
  background-color: rgb(234, 234, 234);
  padding: 20px 0;
  margin-top: auto;
}
footer p {
  margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6 {
  display: block;
  color: rgb(2, 48, 71);
  margin-bottom: 10px;
}

h1 {
  font-size: 2.2rem;
  line-height: 3rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3rem;
    line-height: 3.3rem;
  }
}
@media (min-width: 992px) {
  h1 {
    font-size: 3.8rem;
    line-height: 3.8rem;
  }
}

h2 {
  font-size: 2rem;
  line-height: 2.2rem;
}

h3 {
  font-size: 1.8rem;
  line-height: 2rem;
}
h3 a {
  text-decoration: none;
}
h3 a:hover {
  text-decoration: underline;
}

h4 {
  font-size: 1.6rem;
  line-height: 2rem;
}

h5 {
  font-size: 1.2rem;
  line-height: 1.6rem;
}

h6 {
  font-size: 1rem;
  line-height: 1.2rem;
}

main p {
  margin-bottom: 0.875em;
  line-height: 2rem;
}

main h1 {
  margin-bottom: 20px;
}

section h1 {
  margin-bottom: 40px;
}

main a {
  color: #346aff;
  text-decoration: underline;
  padding: 0 2px;
}
main a:hover {
  color: #023047;
  text-decoration: underline;
}

main ul li {
  padding: 5px 0;
}
main ul li a {
  color: #346aff;
}
main ul li a:hover {
  color: #023047;
}

.inline-pills {
  padding: 0;
  margin: 10px 0;
}
.inline-pills li {
  display: inline-block;
  padding: 12px 10px;
  border-radius: 10px;
  background-color: rgb(2, 48, 71);
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-transform: uppercase;
  margin: 5px 0;
}
@media (min-width: 992px) {
  .inline-pills li {
    margin: 5px 10px 5px 0;
  }
}

table {
  margin: 0.3125em 0;
}
table th {
  background-color: rgb(234, 234, 234);
}
table th, table td {
  padding: 0.3125em;
  border: 1px solid rgb(234, 234, 234);
}

address {
  display: block;
  font-style: italic;
}

main ul, ol {
  margin: 10px 20px;
}
main ul li li, ol li li {
  padding-left: 20px;
  line-height: 2rem;
}

main ul.inline-list {
  padding-left: 0;
  margin-left: 0;
}
main ul.inline-list li {
  display: inline-block;
  padding: 12px 10px 12px 0;
  text-transform: uppercase;
  text-decoration: underline;
  margin: 5px 0;
}

code {
  color: crimson;
  background-color: #f1f1f1;
  padding: 2px;
  font-size: 105%;
}

pre {
  display: block;
  font-family: monospace;
  white-space: pre;
  margin: 2em 0;
  background-color: rgb(234, 234, 234);
  border-left: 5px solid rgb(2, 48, 71);
  position: relative;
  overflow: scroll;
  padding: 20px;
}

q {
  color: gray;
  font-style: italic;
}

dl {
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
}

dt {
  display: block;
  background-color: rgb(234, 234, 234);
  padding: 10px;
  font-weight: bold;
}

dd {
  padding: 10px 20px;
}

blockquote {
  background-color: rgb(2, 48, 71);
  border-left: 5px solid #354a95;
  color: rgb(255, 255, 255);
  padding: 10px;
}

cite {
  background-color: rgb(234, 234, 234);
  padding: 10px 20px;
  margin-left: 10px;
  font-style: italic;
}

.subheading {
  font-weight: 100;
  font-size: 1.2rem;
  line-height: 1.6rem;
  margin: 0 0 60px 0;
  padding: 0 10px;
}

.skip {
  display: none;
}

.button {
  border-radius: 25px;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-decoration: none;
  background-color: rgb(2, 48, 71);
  padding: 15px 40px;
  display: inline-block;
  font-size: 0.8em;
  transition: all 0.2s ease;
  font-weight: bold;
  letter-spacing: 0.1em;
}
.button:hover {
  background-color: #354a95;
}

.pill-container {
  margin: 40px auto;
}
@media (min-width: 768px) {
  .pill-container {
    margin: 40px 0;
  }
}

.pill {
  border-radius: 38px;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-decoration: none;
  background-color: rgb(2, 48, 71);
  padding: 12px 24px;
  margin: 0 auto 10px auto;
  display: inline-block;
  width: auto;
  text-align: center;
  font-size: 0.8em;
  transition: all 0.2s ease;
  font-weight: bold;
  letter-spacing: 0.1em;
}
@media (min-width: 576px) {
  .pill {
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .pill {
    padding: 18px 32px;
    width: auto;
    margin: 0 auto 10px auto;
    font-size: 0.8em;
  }
}
@media (min-width: 992px) {
  .pill {
    font-size: 0.8em;
    padding: 12px 24px;
    margin: 0 10px 0 0;
  }
}
.pill:hover {
  background-color: #354a95;
  color: rgb(255, 255, 255);
}

.button-alt {
  background-color: #354a95;
}
.button-alt:hover {
  background-color: rgb(2, 48, 71);
}

.card-container {
  display: grid;
  row-gap: 30px;
  width: 100%;
  grid-template-columns: 100%;
}
@media (min-width: 768px) {
  .card-container {
    column-gap: 20px;
    row-gap: 20px;
    grid-template-columns: 50% 50%;
  }
}
@media (min-width: 992px) {
  .card-container {
    column-gap: 30px;
    row-gap: 30px;
    grid-template-columns: 33% 33% 33%;
  }
}
.card-container .card {
  width: auto;
  border: 1px solid rgb(234, 234, 234);
  background-color: rgb(234, 234, 234);
  border-radius: 5px;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  padding-bottom: 20px;
}
.card-container .card img {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  justify-self: start;
}
.card-container .card h3 {
  margin: 20px 0;
  padding: 0 20px;
}
.card-container .card h3 a {
  font-size: 1.6rem;
}
@media (max-width: 991.98px) {
  .card-container .card h3 a {
    font-size: 2rem;
    line-height: 2.4rem;
  }
}
.card-container .card a.button {
  margin-left: 18px;
}
.card-container .card p {
  padding: 0 20px;
}
.card-container .card p.small {
  font-size: 1rem;
}
.card-container .card ul {
  padding-left: 20px;
}
.card-container .card ul li {
  padding-left: 5px;
}
.card-container .card time {
  padding: 5px 0;
  margin: 5px 0;
  display: block;
}
.card-container .card footer.entry-footer {
  padding: 0 20px;
  width: 100%;
  background-color: rgb(234, 234, 234);
}
.card-container .card time {
  padding: 10px 20px;
}
.card-container .card footer span.cat-links {
  line-height: 1.8rem;
}
.card-container .card footer a {
  padding: 0;
  color: rgb(2, 48, 71);
  text-decoration: underline;
}
.card-container .card footer a:hover {
  color: #354a95;
}
@media (min-width: 768px) {
  .card-container .card {
    width: auto;
  }
  .card-container .card-full {
    width: 100%;
    column-gap: 0;
  }
}

.spacer {
  margin: 80px auto;
}
@media (min-width: 1200px) {
  .spacer {
    margin: 80px auto;
  }
}

.card-container-full {
  display: flex;
  flex-wrap: wrap;
  row-gap: 40px;
  margin: auto;
  width: 72%;
  justify-items: center;
  margin-bottom: 80px;
}
.card-container-full .card {
  border: 1px solid rgb(234, 234, 234);
  padding: 20px;
}
.card-container-full .card img {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .card-container-full .card {
    width: 100%;
  }
}
@media (min-width: 992px) {
  .card-container-full {
    padding: 0;
  }
  .card-container-full .card img {
    height: 300px;
    width: 300px;
  }
}

.nav-container {
  padding: 20px;
  background: rgb(2, 48, 71);
  display: block;
}
@media (min-width: 768px) {
  .nav-container {
    display: flex;
    justify-content: space-between;
  }
}
.nav-container h1 {
  text-align: center;
}

h1 {
  margin: 20px auto;
}
@media (min-width: 768px) {
  h1 {
    margin: auto 0;
  }
}

h1 a.logo {
  color: rgb(255, 255, 255);
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 400;
  line-height: 0;
  display: block;
  padding: 0;
  font-size: 1.2rem;
}

/*Nav Styles*/
nav ul li {
  list-style: none;
}
nav ul li a {
  color: rgb(234, 234, 234);
  text-decoration: none;
  display: inline-block;
  text-align: center;
  width: 100%;
  border: 2px solid rgb(2, 48, 71);
  transition: all 0.5s ease-out;
  padding: 10px 0;
}
nav ul li a:hover {
  cursor: grab;
  border-radius: 35px;
  border: 2px solid rgb(234, 234, 234);
}
nav ul li a:active, nav ul li a:visited {
  background-color: rgb(2, 48, 71);
}

@media (min-width: 768px) {
  nav ul {
    width: 100%;
  }
  nav ul li {
    display: block;
    width: 100%;
  }
}
@media (min-width: 768px) {
  nav ul li {
    display: inline-block;
    width: auto;
  }
}
@media (min-width: 768px) {
  nav ul li a {
    display: block;
    padding: 5px 20px;
    width: auto;
  }
  nav ul li a:hover {
    cursor: grab;
    border-radius: 35px;
    border: 2px solid rgb(234, 234, 234);
  }
  nav ul li a:active, nav ul li a:visited {
    background-color: rgb(2, 48, 71);
  }
}
/*Social Links Styles*/
.social-links {
  margin: auto 0;
}
.social-links li {
  list-style: none;
  display: inline-block;
  margin: 2px;
}
.social-links li a {
  padding: 8px 12px;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.1);
  transition: all 0.2s ease;
}
.social-links li a:hover {
  background-color: #354a95;
}

/*Section Styles*/
.hero {
  padding: 40px 40px 0 40px;
  display: block;
  background-color: rgb(234, 234, 234);
  margin: auto;
  width: auto;
  background: rgb(255, 255, 255);
  text-align: center;
  max-width: 1140px;
}
@media (min-width: 768px) {
  .hero {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (min-width: 768px) {
  .hero .hero-content {
    max-width: 100%;
  }
}
@media (min-width: 992px) {
  .hero .hero-content {
    padding: 0;
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
  }
}
.hero .card-container {
  display: block;
}
@media (min-width: 992px) {
  .hero .card-container {
    display: flex;
  }
}
.hero .card-container .card {
  padding: 40px 20px;
  margin-bottom: 20px;
  flex: 1;
}
@media (min-width: 992px) {
  .hero .card-container .card {
    margin-bottom: 0;
  }
}
.hero h2 {
  font-size: 2em;
  line-height: 1.2em;
}
@media (min-width: 768px) {
  .hero h2 {
    font-size: 3.2em;
    line-height: 1.2em;
  }
}
.hero p {
  font-size: 1rem;
  font-size: 1rem;
}
@media (min-width: 768px) {
  .hero p {
    font-size: 1.8rem;
  }
}
@media (min-width: 992px) {
  .hero p {
    font-size: 1.8rem;
    line-height: 1.6em;
  }
}

/*Form Styles*/
form {
  position: relative;
}

label,
input,
textarea {
  display: block;
}

input, textarea {
  padding: 10px;
}

textarea {
  width: 100%;
  border-radius: 10px;
}

input {
  padding: 10px 0;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #979797;
}

input.form-button {
  width: auto;
  position: absolute;
  right: 0;
  bottom: -80px;
  padding: 10px 40px;
}

label {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.75em;
  margin-bottom: 0.5em;
}

.wpforms-submit-container button.wpforms-submit {
  background-color: rgb(2, 48, 71) !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 38px !important;
  font-weight: bold !important;
  transition: all 0.2s ease;
  letter-spacing: 0.1em !important;
  padding: 12px 24px !important;
}
.wpforms-submit-container button.wpforms-submit:hover {
  background-color: #354a95 !important;
}

/*Layout Styles*/
.page-banner {
  height: 30vh;
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  background-color: rgb(234, 234, 234);
  padding: 0 40px;
}
.page-banner h1 {
  margin: auto auto 0 auto;
  padding: 0;
  font-size: 3em;
  line-height: 1.2em;
}
@media (min-width: 768px) {
  .page-banner h1 {
    font-size: 3.2em;
  }
}
@media (min-width: 992px) {
  .page-banner h1 {
    font-size: 3.4em;
  }
}
.page-banner p {
  margin: 0 auto auto auto;
}

.bio-photo {
  width: 250px;
  height: 250px;
  border-radius: 50%;
}

@media (max-width: 768px) {
  ul.social-links {
    display: flex;
    justify-content: center;
